#PHome {
    position: relative;
    height: 100%;
}
#PHome h1 {
    position: absolute;
    top: 40%;

    height: 60px;
    line-height: 60px;
    width: 100%;
    margin-top: -30px;
    text-align: center;
    color: #ff9b26;
}
#PHome canvas {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
#PHome a {
    position: absolute;
    top: 75%;
    width: 100%;
	text-align: center;
	color: #ff9b26;
	cursor: pointer;
	text-decoration: underline;
}
#PHome a:active{
	color:#fff;
}

