#PPage2{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
#PPage2 .demo{
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	border:none;
	overflow: hidden;
}
#PPage2 nav{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
#PPage2 nav a{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	margin-left:1px;
	text-align: center;
	color:#333;
	background:#ff9b26;
	-webkit-transition: all .5s;
	transition: all .5s;
	cursor: pointer;
}
#PPage2 nav a:hover{
	-webkit-transform: scale(1.2);
	        transform: scale(1.2);
	-webkit-box-shadow: 0 0 5px #ff9b26;
	        box-shadow: 0 0 5px #ff9b26;
	background:#fff;
}
#PPage2 .nextPage{
	color:#ff9b26;
	text-decoration: underline;
}

#PPage3{
	position: relative;
}
#PPage3 .yz{
	visibility: visible;
	position:absolute;
	right:0;
	height:50px;
	line-height: 50px;
	padding:5px;
	border:2px solid #ff9b26;
	border-radius: 25px;
	color:#ff9b26;
	-webkit-box-shadow: 0 0 5px #000 inset;
	        box-shadow: 0 0 5px #000 inset;
	text-shadow: 0 0 5px #000;
	-webkit-transform: translate(50px,-50px) rotate(-45deg);
	        transform: translate(50px,-50px) rotate(-45deg);
	-webkit-transition: all .5s;
	transition: all .5s;
}
#PPage3 .yz-hide{
	visibility: hidden;
	-webkit-transform: scale(5) rotate(-180deg);
	        transform: scale(5) rotate(-180deg);
	opacity: 0;
}
#PPage3 blockquote{
	padding-left:5px;
	border-left: 2px solid #ff9b26;
	margin:5px;
	color:#acacac;
}
#PPage3 img{
	position: absolute;
	top:50%;
	left:50%;
	width:100%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}







































